import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Subscription } from 'rxjs';
import { map } from 'rxjs/operators';

import * as moment from 'moment';

import { Incident } from 'src/app/services/incidents/incident';
import { IncidentsService } from 'src/app/services/incidents/incidents.service';

export interface WithoutIncidents {
  daysCount: number;
  daysHumanize: string;
}

@Component({
  selector: 'app-incidents-informer',
  templateUrl: './incidents-informer.component.html',
  styleUrls: ['./incidents-informer.component.scss']
})
export class IncidentsInformerComponent implements OnInit {
  public id: string = undefined;
  private webId: string = undefined;
  public withoutIncidents: WithoutIncidents = null;
  public incident: Incident = null;
  public modalIsOpen: boolean = false;
  private subscription: Subscription = null;

  constructor(private route: ActivatedRoute,
    private incidentsService: IncidentsService) {
      this.subscription = this.route.data
      .pipe(map(data => data.webId))
      .subscribe(webId => {
        this.webId = webId;
      });
    }

  public openModal() {
    this.modalIsOpen = true;
  }

  private getIncident() {
    this.incidentsService
      .getItems({
        filter: `OData__ModerationStatus eq 0`,
        top: 1,
        webId: this.webId
      })
      .pipe(map((incidents: Incident[]) => incidents[0])).subscribe(item => {
        this.incident = item;
        if (this.incident) {
          const incidentDate = moment(this.incident.date, 'YYYY-MM-DD');
          const currentDate = moment().startOf('day');
          const countDays = moment.duration(incidentDate.diff(currentDate)).as('days');
          this.withoutIncidents = {
            daysCount: Math.abs(countDays),
            daysHumanize: moment.duration(countDays, 'days').humanize()
          };
        }
      });
  }

  ngOnInit() {
    this.getIncident();
  }
}
