import { Component, OnInit } from '@angular/core';

import { SidebarService } from 'src/app/services/sidebar/sidebar.service';
import { Subscription } from 'rxjs';
import { map } from 'rxjs/operators';
import { ActivatedRoute } from '@angular/router';
import { CompaniesService } from 'src/app/services/companies/companies.service';
import { Company } from 'src/app/services/companies/company';
import { NewsRubric } from 'src/app/services/news-rubrics/news-rubric';

@Component({
  selector: 'app-enterprise-page',
  templateUrl: './enterprise-page.component.html',
  styleUrls: ['./enterprise-page.component.scss']
})
export class EnterprisePageComponent implements OnInit {

  public webId: string = null;
  public currentCompany: Company = null;
  private subscription: Subscription = null;
  public eventRubric: NewsRubric[] = null;
  public id: number = null;

  constructor(private route: ActivatedRoute,
    private sidebarService: SidebarService,
    private companiesService: CompaniesService) {
    this.sidebarService.handleSetSettings({ showSidebar: true, bgColor: '#f7f7f7' });
  }

  ngOnInit() {
    this.id = this.route.snapshot.params.id;
    this.eventRubric = this.route.snapshot.data.newsRubrics.filter(
      (r: NewsRubric) => r.title === 'Событие'
    );
    this.subscription = this.route.data
      .pipe(map(data => data.webId))
      .subscribe(webId => {
        this.webId = webId;
      });
  }

}
